import { api } from './config';

export interface Notificacao {
  id: number;
  titulo: string;
  mensagem: string;
  tipo: 'VENCIMENTO_EMPRESA' | 'PLANO_PAGO' | 'COBRANCA_GERADA' | 'FATURA_PAGA' | 'PARCELA_PAGA' | 'MANUAL' | 'TICKET_MENSAGEM';
  lida: boolean;
  actionLink?: string | null;
  actionLabel?: string | null;
  empresaId?: number | null;
  userId?: number | null;
  metadata?: any;
  createdAt: string;
  updatedAt: string;
  empresa?: {
    id: number;
    nome: string;
  } | null;
  user?: {
    id: number;
    nome: string;
    email: string;
  } | null;
}

export interface CreateNotificacaoRequest {
  titulo: string;
  mensagem: string;
  tipo: Notificacao['tipo'];
  empresaId?: number;
  userId?: number;
  actionLink?: string;
  actionLabel?: string;
  metadata?: any;
}

export interface ListNotificacoesParams {
  page?: number;
  perPage?: number;
  lida?: boolean;
  tipo?: Notificacao['tipo'];
}

export interface PaginatedResponse<T> {
  data: T[];
  pagination: {
    page: number;
    perPage: number;
    total: number;
    totalPages: number;
  };
}

export interface NotificacaoCount {
  count: number;
}

const notificacaoService = {
  /**
   * Lista notificações com paginação e filtros
   */
  list: async (params?: ListNotificacoesParams): Promise<PaginatedResponse<Notificacao>> => {
    const response = await api.get<PaginatedResponse<Notificacao>>('/notificacoes', { params });
    return response.data;
  },

  /**
   * Busca uma notificação por ID
   */
  getById: async (id: number): Promise<Notificacao> => {
    const response = await api.get<{ data: Notificacao }>(`/notificacoes/${id}`);
    return response.data.data;
  },

  /**
   * Cria uma nova notificação
   */
  create: async (data: CreateNotificacaoRequest): Promise<Notificacao | Notificacao[]> => {
    const response = await api.post<{ data: Notificacao | Notificacao[] }>('/notificacoes', data);
    return response.data.data;
  },

  /**
   * Marca uma notificação como lida
   */
  marcarComoLida: async (id: number): Promise<void> => {
    await api.put(`/notificacoes/${id}/lida`);
  },

  /**
   * Marca todas as notificações como lidas
   */
  marcarTodasComoLidas: async (): Promise<void> => {
    await api.put('/notificacoes/lidas');
  },

  /**
   * Conta notificações não lidas
   */
  countNaoLidas: async (): Promise<number> => {
    const response = await api.get<NotificacaoCount>('/notificacoes/count');
    return response.data.count;
  },

  /**
   * Deleta uma notificação
   */
  delete: async (id: number): Promise<void> => {
    await api.delete(`/notificacoes/${id}`);
  }
};

export default notificacaoService;

