import { api, PaginationResponse } from './config';
import { Categoria } from './produtos.service';

const list = (params?: { page?: number; limit?: number; search?: string }) => {
  return api.get<PaginationResponse<Categoria[]>>('/categorias-produtos', { params });
};

const getById = (id: number) => {
  return api.get<Categoria>(`/categorias-produtos/${id}`);
};

const create = (data: Pick<Categoria, 'nome'>) => {
  return api.post<Categoria>('/categorias-produtos', data);
};

const update = (id: number, data: Partial<Pick<Categoria, 'nome'>>) => {
  return api.put<Categoria>(`/categorias-produtos/${id}`, data);
};

const remove = (id: number) => {
  return api.delete(`/categorias-produtos/${id}`);
};

export const categoriaService = {
  list,
  getById,
  create,
  update,
  delete: remove
};
