import { api, ApiResponse, PaginationResponse } from './config';

type status = 'open' | 'close' | 'connecting'
export interface Conexao {
  id: number;
  nome: string;
  numero: string;
  tokenId: string;
  status?: 'CONNECTED' | 'DISCONNECTED' | 'PENDING' | 'CONNECTING';
  qrcode?: string;
  isDefault?: boolean;
  createdAt?: string;
  updatedAt?: string;
  instance?: {
    instanceName: string;
    state: status;
    pairingCode: string | null;
    code: string;
    base64: string;
    count: number;
  };
}

export interface CreateConexaoRequest {
  nome: string;
  numero: string;
  isDefault?: boolean;
  tokenId?: string; // Opcional - será gerado pelo backend
}

export interface UpdateConexaoRequest extends Partial<CreateConexaoRequest> {}

export interface ConexaoResponse {
  message: string;
  data: Conexao;
}

export interface ConexaoListResponse {
  message: string;
  data: Conexao[];
  pagination: {
    page: number;
    limit: number;
    total: number;
    pages: number;
  };
}

export interface ConexaoCountResponse {
  total: number;
}

export interface ConexaoActionResponse {
  message: string;
  data: any;
}

export const conexaoService = {
  // Criar conexão
  async create(data: CreateConexaoRequest): Promise<ApiResponse<Conexao>> {
    const response = await api.post<ApiResponse<Conexao>>('/conexoes', data);
    return response.data;
  },

  // Listar conexões
  async list(params?: { 
    page?: number; 
    limit?: number; 
    search?: string 
  }): Promise<PaginationResponse<Conexao[]>> {
    const response = await api.get<PaginationResponse<Conexao[]>>('/conexoes', {
      params
    });
    return response.data;
  },

  // Obter conexão por ID
  async getById(id: number): Promise<ApiResponse<Conexao>> {
    const response = await api.get<ApiResponse<Conexao>>(`/conexoes/${id}`);
    return response.data;
  },

  // Atualizar conexão
  async update(id: number, data: UpdateConexaoRequest): Promise<ApiResponse<Conexao>> {
    const response = await api.put<ApiResponse<Conexao>>(`/conexoes/${id}`, data);
    return response.data;
  },

  // Excluir conexão
  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/conexoes/${id}`);
    return response.data;
  },

  // Obter total de conexões
  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/conexoes/count/total');
    return response.data;
  },

  // Reiniciar conexão
  async restart(id: number): Promise<ApiResponse<any>> {
    const response = await api.post<ApiResponse<any>>(`/conexoes/${id}/restart`);
    return response.data;
  },

  // Parar conexão
  async stop(id: number): Promise<ApiResponse<any>> {
    const response = await api.post<ApiResponse<any>>(`/conexoes/${id}/stop`);
    return response.data;
  }
};
