import { ApiResponse, ListResponse } from '@/types/api';
import { api } from './config';

export type GatewayType = 'MERCADO_PAGO' | 'OMIE' | 'ASAAS' | 'CORA' | 'EFIPAY';

export interface Gateway {
  id: number;
  gateway: GatewayType;
  nome: string;
  descricao?: string;
  isDefault?: boolean;
  dados: Record<string, any>;
  empresaId?: number;
}

export interface CreateGatewayRequest {
  gateway: GatewayType;
  nome: string;
  descricao?: string;
  isDefault?: boolean;
  dados: Record<string, any>;
  empresaId?: number;
}

export interface UpdateGatewayRequest extends Partial<CreateGatewayRequest> { }

export interface ListGatewayParams {
  page?: number;
  limit?: number;
  search?: string;
  empresaId?: number | null;
}

class GatewayService {
  async create(data: CreateGatewayRequest | FormData): Promise<ApiResponse<Gateway>> {
    const response = await api.post<ApiResponse<Gateway>>('/gateways', data, {
      headers: data instanceof FormData ? { 'Content-Type': 'multipart/form-data' } : { 'Content-Type': 'application/json' }
    });
    return response.data;
  }

  async list(params?: ListGatewayParams): Promise<ListResponse<Gateway>> {
    const response = await api.get<ListResponse<Gateway>>('/gateways', { params });
    return response.data;
  }

  async getById(id: number): Promise<ApiResponse<Gateway>> {
    const response = await api.get<ApiResponse<Gateway>>(`/gateways/${id}`);
    return response.data;
  }

  async update(id: number, data: UpdateGatewayRequest | FormData): Promise<ApiResponse<Gateway>> {
    const response = await api.put<ApiResponse<Gateway>>(`/gateways/${id}`, data, {
      headers: data instanceof FormData ? { 'Content-Type': 'multipart/form-data' } : { 'Content-Type': 'application/json' }
    });
    return response.data;
  }

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/gateways/${id}`);
    return response.data;
  }
}

export const gatewayService = new GatewayService();
