import { api, ApiResponse, PaginationResponse } from './config';

export interface Plano {
  id: number;
  nome: string;
  valor: number;
  dias: number;
  status: 'ATIVO' | 'INATIVO';
  createdAt: string;
  updatedAt: string;
}

export interface CompraPlano {
  id: number;
  planoId: number;
  empresaId: number;
  status: 'PENDENTE' | 'PAGO';
  valor: number;
  gatewayId?: number;
  id_payment?: string;
  copiacola?: string;
  qrcode?: string;
  codigobarra?: string;
  vencimento?: string;
  dataPagamento?: string;
  createdAt: string;
  updatedAt: string;
  plano?: Plano;
  empresa?: any;
  gateway?: any;
}

export interface CreatePlanoRequest {
  nome: string;
  valor: number;
  dias: number;
  status?: 'ATIVO' | 'INATIVO';
}

export interface UpdatePlanoRequest extends Partial<CreatePlanoRequest> {}

export interface ListPlanosParams {
  page?: number;
  perPage?: number;
  search?: string;
  ativo?: boolean;
}

export interface ComprarPlanoRequest {
  planoId: number;
}

export interface PlanoAtualResponse {
  planoAtual: CompraPlano | null;
  comprasPendentes: CompraPlano[];
}

export const planoService = {
  async create(data: CreatePlanoRequest): Promise<ApiResponse<Plano>> {
    const response = await api.post<ApiResponse<Plano>>('/planos', data);
    return response.data;
  },

  async list(params?: ListPlanosParams): Promise<PaginationResponse<Plano[]>> {
    const response = await api.get<PaginationResponse<Plano[]>>('/planos', {
      params: params || { page: 1, perPage: 10 }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<Plano>> {
    const response = await api.get<ApiResponse<Plano>>(`/planos/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdatePlanoRequest): Promise<ApiResponse<Plano>> {
    const response = await api.put<ApiResponse<Plano>>(`/planos/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/planos/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/planos/count/total');
    return response.data;
  }
};

export const compraPlanoService = {
  async comprar(data: ComprarPlanoRequest): Promise<ApiResponse<CompraPlano>> {
    const response = await api.post<ApiResponse<CompraPlano>>('/compra-planos/contratar', data);
    return response.data;
  },

  async list(page = 1, perPage = 10): Promise<PaginationResponse<CompraPlano[]>> {
    const response = await api.get<PaginationResponse<CompraPlano[]>>('/compra-planos', {
      params: { page, perPage }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<CompraPlano>> {
    const response = await api.get<ApiResponse<CompraPlano>>(`/compra-planos/${id}`);
    return response.data;
  },

  async getPlanoAtual(): Promise<ApiResponse<PlanoAtualResponse>> {
    const response = await api.get<ApiResponse<PlanoAtualResponse>>('/compra-planos/empresa/atual');
    return response.data;
  },

  async gerarCobranca(id: number): Promise<ApiResponse<CompraPlano>> {
    const response = await api.post<ApiResponse<CompraPlano>>(`/compra-planos/${id}/gerar-cobranca`);
    return response.data;
  }
};
