import { api, ApiResponse, PaginationResponse } from './config';
import { Produto } from './produto.service';
import { Fornecedor } from './fornecedor.service';

export interface ItemCotacao {
  id: number;
  fornecedorId: number;
  produtoId: number;
  cotacaoId: number;
  valorUnitario: string;
  quantidade: number;
  aprovada: boolean;
  vencedora: boolean;
  produto?: Produto;
  fornecedor?: Fornecedor;
}

export interface CreateItemCotacaoDTO {
  fornecedorId: number;
  produtoId: number;
  cotacaoId: number;
  valorUnitario: string;
  quantidade: number;
  aprovada?: boolean;
  vencedora?: boolean;
  produtoIds?: number[];
  fornecedorIds?: number[];
  orderBy?: 'preco_asc' | 'preco_desc' | 'quantidade_asc' | 'quantidade_desc';
}

export interface UpdateItemCotacaoDTO extends Partial<CreateItemCotacaoDTO> {}

export const itemCotacaoService = {
  async create(data: CreateItemCotacaoDTO): Promise<ApiResponse<ItemCotacao>> {
    const response = await api.post<ApiResponse<ItemCotacao>>('/cotacoes-items', data);
    return response.data;
  },

  async list({
    page = 1,
    limit = 9,
    search = '',
    cotacaoId,
    aprovados,
    vencedores,
    produtoIds,
    fornecedorIds,
    orderBy
  }: {
    page?: number;
    limit?: number;
    search?: string;
    cotacaoId?: number;
    aprovados?: boolean;
    vencedores?: boolean;
    produtoIds?: number[];
    fornecedorIds?: number[];
    orderBy?: 'preco_asc' | 'preco_desc' | 'quantidade_asc' | 'quantidade_desc';
  } = {}): Promise<PaginationResponse<ItemCotacao[]>> {
    const response = await api.get<PaginationResponse<ItemCotacao[]>>('/cotacoes-items', {
      params: { page, limit, search, cotacaoId, aprovados, vencedores, produtoIds: produtoIds?.toString(), fornecedorIds: fornecedorIds?.toString(), orderBy }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<ItemCotacao>> {
    const response = await api.get<ApiResponse<ItemCotacao>>(`/cotacoes-items/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateItemCotacaoDTO): Promise<ApiResponse<ItemCotacao>> {
    const response = await api.put<ApiResponse<ItemCotacao>>(`/cotacoes-items/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/cotacoes-items/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/cotacoes-items/count/total');
    return response.data;
  }
};
