import { api, ApiResponse } from './config';

export interface User {
  id: number;
  nome: string;
  email: string;
  celular: string;
  role: 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE';
}

export interface LoginRequest {
  email: string;
  senha: string;
}

export interface LoginResponse {
  token: string;
  user: User;
}

export interface LoginByTokenResponse {
  token: string;
  user: User & {
    empresaId?: number;
    clienteId?: number;
    empresa?: {
      id: number;
      nome: string;
    };
    cliente?: {
      id: number;
      nome: string;
    };
  };
}

export interface RegisterRequest {
  nome: string;
  email: string;
  senha: string;
  celular: string;
}

export interface ChangePasswordRequest {
  senhaAtual: string;
  novaSenha: string;
}

export interface ResetPasswordRequest {
  email: string;
}

export interface ResetPasswordResponse {
  nextRequestAt?: string;
}

export const authService = {
  async login(data: LoginRequest): Promise<ApiResponse<LoginResponse>> {
    const response = await api.post<ApiResponse<LoginResponse>>('/auth/login', data);
    return response.data;
  },

  async loginByToken(token: string): Promise<ApiResponse<LoginByTokenResponse>> {
    const response = await api.post<ApiResponse<LoginByTokenResponse>>(`/auth/login/${token}`);
    return response.data;
  },

  async register(data: RegisterRequest): Promise<ApiResponse<User>> {
    const response = await api.post<ApiResponse<User>>('/auth/register', data);
    return response.data;
  },

  async getMe(): Promise<ApiResponse<User>> {
    const response = await api.get<ApiResponse<User>>('/auth/me');
    return response.data;
  },

  async changePassword(data: ChangePasswordRequest): Promise<ApiResponse<void>> {
    const response = await api.post<ApiResponse<void>>('/auth/change-password', data);
    return response.data;
  },

  async resetPassword(data: ResetPasswordRequest): Promise<ApiResponse<ResetPasswordResponse | null>> {
    const response = await api.post<ApiResponse<ResetPasswordResponse | null>>('/auth/reset-password', data);
    return response.data;
  },

  async validateToken(token: string): Promise<ApiResponse<boolean>> {
    const response = await api.post<ApiResponse<boolean>>('/auth/validate-token', { token });
    return response.data;
  }
};
