import { api, ApiResponse } from './config';

export type TipoMensagem = 'A_VENCER' | 'NO_DIA' | 'VENCIDA' | 'COMPROVANTE' | 'MANUAL' | 'FINALIZACAO';

export interface Mensagem {
  id: number;
  mensagem: string;
  tipo: TipoMensagem;
  status: boolean;
  hora: string;
  dias: number;
}

export interface CreateMensagemRequest {
  mensagem: string;
  tipo: TipoMensagem;
  status: boolean;
  hora: string;
  dias: number;
}

export interface UpdateMensagemRequest extends Partial<CreateMensagemRequest> {}

export const mensagemService = {
  async create(data: CreateMensagemRequest): Promise<ApiResponse<Mensagem>> {
    const response = await api.post<ApiResponse<Mensagem>>('/mensagens', data);
    return response.data;
  },

  async list(): Promise<ApiResponse<Mensagem[]>> {
    const response = await api.get<ApiResponse<Mensagem[]>>('/mensagens');
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<Mensagem>> {
    const response = await api.get<ApiResponse<Mensagem>>(`/mensagens/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateMensagemRequest): Promise<ApiResponse<Mensagem>> {
    const response = await api.put<ApiResponse<Mensagem>>(`/mensagens/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/mensagens/${id}`);
    return response.data;
  }
};
