import { api, ApiResponse } from './config';

interface PaginationResponse<T> extends ApiResponse<T> {
  pagination: {
    total: number;
    page: number;
    pages: number;
    limit: number;
  }
}

export interface Cliente {
  id: number;
  nome: string;
  email: string | null;
  celular: string | null;
  cpfCnpj: string | null;
  cep: string | null;
  rua: string | null;
  numero: string | null;
  complemento: string | null;
  bairro: string | null;
  cidade: string | null;
  estado: string | null;
  extras: { [key: string]: string } | null; // Objeto simples de chave-valor
  nascimento: string | null;
  createdAt?: string;
  updatedAt?: string;
}

export interface CreateClienteRequest {
  nome: string;
  email?: string | null;
  celular?: string | null;
  cpfCnpj?: string | null;
  cep?: string | null;
  rua?: string | null;
  numero?: string | null;
  complemento?: string | null;
  bairro?: string | null;
  cidade?: string | null;
  estado?: string | null;
  extras?: { [key: string]: string } | null; // Objeto simples de chave-valor
  nascimento?: string | null;
}

export type UpdateClienteRequest = Partial<Omit<CreateClienteRequest, 'cpfCnpj'>>;

export type ClienteList = PaginationResponse<Cliente[]>;

export const clienteService = {
  async create(data: CreateClienteRequest): Promise<ApiResponse<Cliente>> {
    const response = await api.post<ApiResponse<Cliente>>('/clientes', data);
    return response.data;
  },

  async list(params?: { page?: number; limit?: number; search?: string }): Promise<ClienteList> {
    const response = await api.get<ClienteList>('/clientes', {
      params
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<Cliente>> {
    const response = await api.get<ApiResponse<Cliente>>(`/clientes/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateClienteRequest): Promise<ApiResponse<Cliente>> {
    const response = await api.put<ApiResponse<Cliente>>(`/clientes/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/clientes/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/clientes/count/total');
    return response.data;
  }
};
