import { api, ApiResponse } from './config';

export interface User {
  id: number;
  nome: string;
  email: string;
  celular: string;
  role: 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE';
  empresaId?: number;
  empresa?: {
    id: number;
    nome: string;
  };
  createdAt: string;
  updatedAt: string;
}

export interface ListUsersParams {
  page?: number;
  limit?: number;
  search?: string;
  role?: string;
}

export interface PaginatedResponse<T> {
  data: T[];
  pagination: {
    currentPage: number;
    totalPages: number;
    totalItems: number;
    itemsPerPage: number;
  };
}

export interface CreateUserRequest {
  nome: string;
  email: string;
  senha: string;
  celular?: string;
  role: User['role'];
}

export interface UpdateUserRequest {
  nome?: string;
  email?: string;
  senha?: string;
  celular?: string;
  role?: User['role'];
}

export const usersService = {
  async list(params?: ListUsersParams): Promise<PaginatedResponse<User>> {
    const response = await api.get<PaginatedResponse<User>>('/users', { params });
    return response.data;
  },

  async get(id: number): Promise<ApiResponse<User>> {
    const response = await api.get<ApiResponse<User>>(`/users/${id}`);
    return response.data;
  },

  async create(data: CreateUserRequest): Promise<ApiResponse<User>> {
    const response = await api.post<ApiResponse<User>>('/users', data);
    return response.data;
  },

  async update(id: number, data: UpdateUserRequest): Promise<ApiResponse<User>> {
    const response = await api.put<ApiResponse<User>>(`/users/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/users/${id}`);
    return response.data;
  }
};
