import { api, ApiResponse, PaginationResponse } from './config';

export interface Categoria {
  id: number;
  nome: string;
}

export interface Produto {
  id: number;
  sku: string;
  nome: string;
  descricao: string;
  valor: number;
  estoque: number;
  categoriaId: number;
  categoria: Categoria;
}

export interface CreateCategoriaRequest {
  nome: string;
}

export interface CreateProdutoRequest {
  sku: string;
  nome: string;
  descricao: string;
  valor: number;
  estoque: number;
  categoriaId: number;
}

export interface UpdateProdutoRequest extends Partial<CreateProdutoRequest> {}

export const categoriaService = {
  async create(data: CreateCategoriaRequest): Promise<ApiResponse<Categoria>> {
    const response = await api.post<ApiResponse<Categoria>>('/categorias', data);
    return response.data;
  },

  async list(): Promise<ApiResponse<Categoria[]>> {
    const response = await api.get<ApiResponse<Categoria[]>>('/categorias');
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/categorias/${id}`);
    return response.data;
  }
};

export const produtoService = {
  async create(data: CreateProdutoRequest): Promise<ApiResponse<Produto>> {
    const response = await api.post<ApiResponse<Produto>>('/produtos', data);
    return response.data;
  },

  async list({page = 1, limit = 10, search = ''}): Promise<PaginationResponse<Produto[]>> {
    const response = await api.get<PaginationResponse<Produto[]>>('/produtos', {
      params: { page, limit, search }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<Produto>> {
    const response = await api.get<ApiResponse<Produto>>(`/produtos/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateProdutoRequest): Promise<ApiResponse<Produto>> {
    const response = await api.put<ApiResponse<Produto>>(`/produtos/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/produtos/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/produtos/count/total');
    return response.data;
  }
};
