import { api } from './config';
import { PaginationResponse } from '../api';

export type CategoriaFornecedor = {
  id: number;
  nome: string;
};

export type CreateCategoriaFornecedorDTO = Pick<CategoriaFornecedor, 'nome'>;

const list = () => {
  return api.get<PaginationResponse<CategoriaFornecedor[]>>('/categorias-fornecedores');
};

const getById = (id: number) => {
  return api.get<CategoriaFornecedor>(`/categorias-fornecedores/${id}`);
};

const create = (data: CreateCategoriaFornecedorDTO) => {
  return api.post<CategoriaFornecedor>('/categorias-fornecedores', data);
};

const update = (id: number, data: CreateCategoriaFornecedorDTO) => {
  return api.put<CategoriaFornecedor>(`/categorias-fornecedores/${id}`, data);
};

const remove = (id: number) => {
  return api.delete(`/categorias-fornecedores/${id}`);
};

export const categoriaFornecedorService = {
  list,
  getById,
  create,
  update,
  delete: remove
};
