import { api, ApiResponse, PaginationResponse } from './config';

export type TipoMovimento = 'ENTRADA' | 'SAIDA';

import { Produto } from './produto.service';
import { Fornecedor } from './fornecedor.service';

export interface MovimentoEstoque {
  id: number;
  tipo: TipoMovimento;
  quantidade: number;
  valorUnitario: number;
  produtoId: number;
  fornecedorId: number;
  data: Date;
  observacoes?: string;
  produto?: Produto;
  fornecedor?: Fornecedor;
}

export interface CreateMovimentoEstoqueRequest {
  tipo: TipoMovimento;
  quantidade: number;
  valorUnitario: number;
  produtoId: number;
  fornecedorId: number;
  data: Date;
  observacoes?: string;
}

export interface UpdateMovimentoEstoqueRequest extends Partial<CreateMovimentoEstoqueRequest> {}

export const movimentoEstoqueService = {
  async create(data: CreateMovimentoEstoqueRequest): Promise<ApiResponse<MovimentoEstoque>> {
    const response = await api.post<ApiResponse<MovimentoEstoque>>('/movimentos-estoque', data);
    return response.data;
  },

  async list({
    page = 1,
    perPage = 10,
    search = '',
    tipo,
    produtoIds,
    orderBy
  }: {
    page?: number;
    perPage?: number;
    search?: string;
    tipo?: TipoMovimento;
    produtoIds?: number[];
    orderBy?: 'data_asc' | 'data_desc' | 'quantidade_asc' | 'quantidade_desc';
  } = {}): Promise<PaginationResponse<MovimentoEstoque[]>> {
    const response = await api.get<PaginationResponse<MovimentoEstoque[]>>('/movimentos-estoque', {
      params: { 
        page, 
        limit: perPage, 
        search, 
        tipo,
        produtoIds: produtoIds?.toString(),
        orderBy 
      }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<MovimentoEstoque>> {
    const response = await api.get<ApiResponse<MovimentoEstoque>>(`/movimentos-estoque/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateMovimentoEstoqueRequest): Promise<ApiResponse<MovimentoEstoque>> {
    const response = await api.put<ApiResponse<MovimentoEstoque>>(`/movimentos-estoque/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/movimentos-estoque/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/movimentos-estoque/count/total');
    return response.data;
  }
};
