import { api } from './config';
import type { PaginatedResponse } from './notificacao.service';

export type TipoTicket = 'PROBLEMA' | 'AJUDA' | 'MELHORIA' | 'SUGESTAO';
export type StatusTicket = 'ABERTO' | 'EM_ANDAMENTO' | 'RESOLVIDO' | 'FECHADO';
export type PrioridadeTicket = 'BAIXA' | 'MEDIA' | 'ALTA' | 'URGENTE';

export interface Ticket {
  id: number;
  uuid: string;
  titulo: string;
  descricao: string;
  tipo: TipoTicket;
  status: StatusTicket;
  prioridade: PrioridadeTicket;
  anexos?: any | null;
  criadorId: number;
  empresaId: number;
  createdAt: string;
  updatedAt: string;
  criador?: {
    id: number;
    nome: string;
    email: string;
    role: string;
  };
  empresa?: {
    id: number;
    nome: string;
  };
  mensagens?: TicketMensagem[];
  _count?: {
    mensagens: number;
  };
}

export interface TicketMensagem {
  id: number;
  ticketId: number;
  userId: number;
  mensagem: string;
  anexos?: any | null;
  createdAt: string;
  updatedAt: string;
  user?: {
    id: number;
    nome: string;
    email: string;
    role: string;
  };
}

export interface CreateTicketRequest {
  titulo: string;
  descricao: string;
  tipo: TipoTicket;
  prioridade?: PrioridadeTicket;
  anexos?: any;
}

export interface UpdateTicketRequest {
  status?: StatusTicket;
  prioridade?: PrioridadeTicket;
}

export interface AddMensagemRequest {
  mensagem: string;
  anexos?: any;
}

export interface ListTicketsParams {
  page?: number;
  perPage?: number;
  status?: StatusTicket;
  tipo?: TipoTicket;
  prioridade?: PrioridadeTicket;
  empresaId?: number;
  search?: string;
}

class TicketService {
  /**
   * Lista tickets com paginação e filtros
   */
  async list(params?: ListTicketsParams): Promise<PaginatedResponse<Ticket>> {
    const queryParams = new URLSearchParams();
    
    if (params?.page) queryParams.append('page', params.page.toString());
    if (params?.perPage) queryParams.append('perPage', params.perPage.toString());
    if (params?.status) queryParams.append('status', params.status);
    if (params?.tipo) queryParams.append('tipo', params.tipo);
    if (params?.prioridade) queryParams.append('prioridade', params.prioridade);
    if (params?.empresaId) queryParams.append('empresaId', params.empresaId.toString());
    if (params?.search) queryParams.append('search', params.search);

    const response = await api.get(`/tickets?${queryParams.toString()}`);
    return response.data;
  }

  /**
   * Busca ticket por ID
   */
  async getById(id: number): Promise<{ data: Ticket }> {
    const response = await api.get(`/tickets/${id}`);
    return response.data;
  }

  /**
   * Cria um novo ticket
   */
  async create(data: CreateTicketRequest, files?: File[]): Promise<{ data: Ticket }> {
    const formData = new FormData();
    
    formData.append('titulo', data.titulo);
    formData.append('descricao', data.descricao);
    formData.append('tipo', data.tipo);
    if (data.prioridade) {
      formData.append('prioridade', data.prioridade);
    }
    if (data.anexos) {
      formData.append('anexos', JSON.stringify(data.anexos));
    }

    if (files && files.length > 0) {
      files.forEach((file) => {
        formData.append('anexos', file);
      });
    }

    const response = await api.post('/tickets', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
    return response.data;
  }

  /**
   * Atualiza ticket (apenas SUPERADMIN)
   */
  async update(id: number, data: UpdateTicketRequest): Promise<{ data: Ticket }> {
    const response = await api.put(`/tickets/${id}`, data);
    return response.data;
  }

  /**
   * Adiciona mensagem ao ticket
   */
  async addMensagem(ticketId: number, data: AddMensagemRequest, files?: File[]): Promise<{ data: TicketMensagem }> {
    const formData = new FormData();
    
    formData.append('mensagem', data.mensagem);
    if (data.anexos) {
      formData.append('anexos', JSON.stringify(data.anexos));
    }

    if (files && files.length > 0) {
      files.forEach((file) => {
        formData.append('anexos', file);
      });
    }

    const response = await api.post(`/tickets/${ticketId}/mensagens`, formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
    return response.data;
  }

  /**
   * Deleta ticket
   */
  async delete(id: number): Promise<{ message: string }> {
    const response = await api.delete(`/tickets/${id}`);
    return response.data;
  }
}

export const ticketService = new TicketService();
export default ticketService;

