import { api } from './config';
import { PaginationResponse } from '../api';

export type Fornecedor = {
  id: number;
  nome: string;
  cpfCnpj: string;
  email: string;
  celular: string;
  categoriaId: number | null;
};

export type CreateFornecedorDTO = {
  nome: string;
  cpfCnpj: string;
  email: string;
  celular: string;
  categoriaId: number | null;
};
export type UpdateFornecedorDTO = Partial<CreateFornecedorDTO>;

const list = (params?: { page?: number; limit?: number; search?: string }) => {
  return api.get<PaginationResponse<Fornecedor[]>>('/fornecedores', { params });
};

const getById = (id: number) => {
  return api.get<Fornecedor>(`/fornecedores/${id}`);
};

const create = (data: CreateFornecedorDTO) => {
  return api.post<Fornecedor>('/fornecedores', data);
};

const update = (id: number, data: UpdateFornecedorDTO) => {
  return api.put<Fornecedor>(`/fornecedores/${id}`, data);
};

const remove = (id: number) => {
  return api.delete(`/fornecedores/${id}`);
};

export const fornecedorService = {
  list,
  getById,
  create,
  update,
  delete: remove
};
