import Cookies from 'js-cookie';
import { getAppConfig } from '@/hooks/useAppConfig';

export const setCookie = (name: string, value: string, options = {}) => {
  // Tentar obter da configuração dinâmica primeiro
  const config = getAppConfig();
  const isProduction = config ? 
    config.apiEnv === 'production' : 
    (process.env.NEXT_PUBLIC_API_ENV || process.env.NODE_ENV) === 'production';

  Cookies.set(name, value, {
    ...options,
    secure: isProduction,
    sameSite: 'lax',
  });
};

export const getCookie = (name: string) => {
  return Cookies.get(name);
};

export const removeCookie = (name: string) => {
  Cookies.remove(name);
};

const token = {
  get: () => getCookie('token'),
  set: (value: string, options = {}) => setCookie('token', value, options),
  remove: () => removeCookie('token'),
};

const user = {
  get: () => getCookie('user'),
  set: (value: string, options = {}) => setCookie('user', value, options),
  remove: () => removeCookie('user'),
};

export const cookies = {
  user,
  token,
  auth: {
    clear: () => {
      user.remove();
      token.remove();
    }
  }
}