import { api, ApiResponse, PaginationResponse } from './config';

export interface Empresa {
  id: number;
  nome: string;
  cpfCnpj?: string;
  celular?: string;
  cep?: string;
  cidade?: string;
  estado?: string;
  bairro?: string;
  rua?: string;
  numero?: string;
  vencimento: string;
  createdAt: string;
  updatedAt: string;
}

export interface CreateEmpresaRequest {
  nome: string;
  cpfCnpj: string;
  celular: string;
  cep: string;
  cidade: string;
  estado: string;
  bairro: string;
  rua: string;
  numero: string;
}

export interface UpdateEmpresaRequest extends Partial<CreateEmpresaRequest> {}

export interface ListEmpresasParams {
  page?: number;
  perPage?: number;
  search?: string;
}

// Ajustado para refletir o retorno real do endpoint /empresas/{id}/dashboard
export interface DashboardData {
  empresa: {
    id: number;
    uuid: string;
    nome: string;
    cpfCnpj: string | null;
    celular: string | null;
    cep: string | null;
    cidade: string | null;
    estado: string | null;
    bairro: string | null;
    rua: string | null;
    numero: string | null;
    vencimento: string;
    createdAt: string;
    updatedAt: string;
    usuarios: Array<{ id: number; nome: string; email: string; role: string }>;
    clientes: Array<{ id: number; nome: string; cpfCnpj: string }>;
    produtos: Array<{ id: number; nome: string; sku: string; valor: string; estoque: number }>;
    vendas: Array<{ id: number; uuid: string; valor: string; status: string; createdAt: string }>;
    _count: {
      clientes: number;
      produtos: number;
      fornecedores: number;
      vendas: number;
      usuarios: number;
    };
  };
  estatisticas: {
    totalClientes: number;
    totalProdutos: number;
    totalFornecedores: number;
    totalVendas: number;
    totalUsuarios: number;
    totalVendasValor: number; // soma de todas as vendas
    vendasPagas: number; // soma do valor de vendas com status PAGO
    vendasPendentes: number; // soma do valor de vendas com status PENDENTE
  };
}

export const empresaService = {
  async create(data: CreateEmpresaRequest): Promise<ApiResponse<Empresa>> {
    const response = await api.post<ApiResponse<Empresa>>('/empresas', data);
    return response.data;
  },

  async list(params?: ListEmpresasParams): Promise<PaginationResponse<Empresa[]>> {
    const response = await api.get<PaginationResponse<Empresa[]>>('/empresas', {
      params: params || { page: 1, perPage: 10 }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<Empresa>> {
    const response = await api.get<ApiResponse<Empresa>>(`/empresas/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateEmpresaRequest): Promise<ApiResponse<Empresa>> {
    const response = await api.put<ApiResponse<Empresa>>(`/empresas/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/empresas/${id}`);
    return response.data;
  },

  async getDashboard(id: number): Promise<ApiResponse<DashboardData>> {
    const response = await api.get<ApiResponse<DashboardData>>(`/empresas/${id}/dashboard`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/empresas/count/total');
    return response.data;
  }
};
