import { api, ApiResponse } from './config';

export interface CompanyTopClients {
  nome: string;
  clientes: number;
}

export interface CompanyStats {
  total: number;
  active: number;
  expired: number;
  expiring: number;
  topByClients: CompanyTopClients[];
}

export interface UserStats {
  total: number;
  breakdown: {
    superadmin: number;
    admin: number;
    gestor: number;
    vendedor: number;
    cliente: number;
  };
}

export interface PlanMonthlyPoint {
  label: string;
  key: string;
  revenue: number;
  transactions: number;
}

export interface PlanTopItem {
  nome: string;
  revenue: number;
  purchases: number;
  averageTicket: number;
}

export interface PlanRecentPurchase {
  id: number;
  empresa: string;
  plano: string;
  valor: number;
  status: string;
  createdAt: string;
  dataPagamento: string | null;
}

export interface PlanStatusDistribution {
  status: string;
  total: number;
  revenue: number;
}

export interface PlanStats {
  catalog: number;
  activePlans: number;
  inactivePlans: number;
  totalRevenue: number;
  pendingRevenue: number;
  totalPurchases: number;
  paidPurchases: number;
  pendingPurchases: number;
  averageTicket: number;
  monthlyRevenue: PlanMonthlyPoint[];
  statusDistribution: PlanStatusDistribution[];
  topPlans: PlanTopItem[];
  topCompanies: PlanTopItem[];
  recentPurchases: PlanRecentPurchase[];
}

export interface AdminStats {
  companies: CompanyStats;
  users: UserStats;
  plans: PlanStats;
}

export const adminStatsService = {
  async getStats(): Promise<ApiResponse<AdminStats>> {
    const response = await api.get<ApiResponse<AdminStats>>('/admin/stats');
    return response.data;
  },
};
