import { api } from './index';

export interface Vendedor {
  id: number;
  nome: string;
  email: string;
  celular: string;
  empresaId: number;
  createdAt: string;
  updatedAt: string;
}

export interface VendedorCreateRequest {
  nome: string;
  email: string;
  senha: string;
  celular: string;
  empresaId?: number;
}

export interface VendedorUpdateRequest {
  nome?: string;
  email?: string;
  senha?: string;
  celular?: string;
  empresaId?: number;
}

export interface VendedorListResponse {
  message: string;
  data: Vendedor[];
  pagination: {
    page: number;
    limit: number;
    total: number;
    pages: number;
  };
}

export interface VendedorResponse {
  message: string;
  data: Vendedor;
}

export interface VendedorCountResponse {
  message: string;
  data: {
    count: number;
  };
}

export const vendedorService = {
  // Listar vendedores
  list: async (params?: {
    page?: number;
    limit?: number;
    search?: string;
  }): Promise<VendedorListResponse> => {
    const response = await api.get<VendedorListResponse>('/vendedores', { params });
    return response.data;
  },

  // Obter vendedor por ID
  getById: async (id: number): Promise<VendedorResponse> => {
    const response = await api.get<VendedorResponse>(`/vendedores/${id}`);
    return response.data;
  },

  // Criar vendedor
  create: async (data: VendedorCreateRequest): Promise<VendedorResponse> => {
    const response = await api.post<VendedorResponse>('/vendedores', data);
    return response.data;
  },

  // Atualizar vendedor
  update: async (id: number, data: VendedorUpdateRequest): Promise<VendedorResponse> => {
    const response = await api.put<VendedorResponse>(`/vendedores/${id}`, data);
    return response.data;
  },

  // Deletar vendedor
  delete: async (id: number): Promise<{ message: string }> => {
    const response = await api.delete<{ message: string }>(`/vendedores/${id}`);
    return response.data;
  },

  // Contar total de vendedores
  count: async (): Promise<VendedorCountResponse> => {
    const response = await api.get<VendedorCountResponse>('/vendedores/count/total');
    return response.data;
  },
};
