import { api, ApiResponse, PaginationResponse } from './config';
import { ItemCotacao } from './item-cotacao.service';

export type StatusCotacao = 'EM_ANDAMENTO' | 'FECHADA';
export type TipoCotacao = 'IMEDIATA' | 'PROGRAMADA';

export interface Cotacao {
  id: number;
  nome: string;
  descricao?: string | null;
  dataAbertura: Date;
  dataFechamento: Date | null;
  status: StatusCotacao;
  tipo: TipoCotacao;
  itens?: ItemCotacao[];
}

export interface CreateCotacaoRequest {
  nome: string;
  descricao?: string | null;
  dataAbertura: Date;
  dataFechamento?: Date | null;
  status?: StatusCotacao;
  tipo: TipoCotacao;
  itens?: ItemCotacao[];
}
export type CreateCotacaoDTO = Omit<Cotacao, 'id'>;

export interface UpdateCotacaoRequest {
  status?: StatusCotacao;
  dataFechamento?: string;
  produtosFornecedores?: Array<{
    id: number;
    aprovada?: boolean;
    vencedora?: boolean;
  }>;
}

export const cotacaoService = {
  async create(data: CreateCotacaoRequest): Promise<ApiResponse<Cotacao>> {
    const response = await api.post<ApiResponse<Cotacao>>('/cotacoes', data);
    return response.data;
  },

  async list({ page = 1, limit = 10, search = '' }): Promise<PaginationResponse<Cotacao[]>> {
    const response = await api.get<PaginationResponse<Cotacao[]>>('/cotacoes', {
      params: { page, limit }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<Cotacao>> {
    const response = await api.get<ApiResponse<Cotacao>>(`/cotacoes/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateCotacaoRequest): Promise<ApiResponse<Cotacao>> {
    const response = await api.put<ApiResponse<Cotacao>>(`/cotacoes/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/cotacoes/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/cotacoes/count/total');
    return response.data;
  }
};
