import { api, ApiResponse, PaginationResponse } from './config';
import { Vendedor } from './vendedor.service';
import { Cliente } from './cliente.service';

export type StatusPagamento = 'PENDENTE' | 'PAGO';
export type TipoPagamento = 'PIX' | 'BOLETO';
export type StatusVendaFinal = 'PENDING' | 'APPROVED' | 'CANCELLED';

export interface VendaProduto {
  id: number;
  produtoId: number;
  produto: {
    id: number;
    nome: string;
    valor: number;
  };
  quantidade: number;
  valorUnitario: number;
  valorTotal: number;
}

export interface Venda {
  id: number;
  uuid: string;
  valor: number;
  status: StatusPagamento;
  recorrencia: number;
  quantidadeRecorrencia: number;
  valorTotal: number;
  inicio: Date;
  fim: Date;
  vendedorId: number;
  empresaId: number;
  clienteId: number;
  conexaoId?: number;
  createdAt: Date;
  updatedAt: Date;
  vendedor?: {
    id: number;
    nome: string;
  };
  cliente?: {
    id: number;
    nome: string;
  };
  empresa?: {
    id: number;
    nome: string;
  };
  gateway?: {
    id: number;
    nome: string;
  };
  conexao?: {
    id: number;
    nome: string;
  };
  produtos?: VendaProduto[];
}

export interface VendaFinal {
  id: number;
  uuid: string;
  valor: number;
  status: StatusVendaFinal;
  id_payment?: string;
  type: TipoPagamento;
  copiacola?: string;
  qrcode?: string;
  codigobarra?: string;
  vencimento: Date;
  dataPagamento?: Date;
  vendaId: number;
  gatewayId?: number;
  conexaoId?: number;
  createdAt: Date;
  updatedAt: Date;
}

export interface CreateVendaRequest {
  valor: number;
  valorTotal: number;
  inicio: Date;
  clienteId: number;
  vendedorId: number;
  type: TipoPagamento;
  produtos?: Array<{
    produtoId: number;
    quantidade: number;
  }>;
  status?: StatusPagamento;
  recorrencia?: number;
  quantidadeRecorrencia?: number;
  gatewayId?: number;
  conexaoId?: number;
}

export interface UpdateVendaRequest extends Partial<Omit<CreateVendaRequest, 'vendedorId'>> {
  vendedorId?: number;
}

export const vendaService = {
  async create(data: CreateVendaRequest): Promise<ApiResponse<Venda>> {
    const response = await api.post<ApiResponse<Venda>>('/vendas', data);
    return response.data;
  },

  async list(params: { page?: number; limit?: number; search?: string } = {}): Promise<PaginationResponse<Venda[]>> {
    const { page = 1, limit = 10, search } = params;
    const response = await api.get<PaginationResponse<Venda[]>>('/vendas', {
      params: { page, limit, search }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<Venda>> {
    const response = await api.get<ApiResponse<Venda>>(`/vendas/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateVendaRequest): Promise<ApiResponse<Venda>> {
    const response = await api.put<ApiResponse<Venda>>(`/vendas/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/vendas/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/vendas/count/total');
    return response.data;
  }
};

export interface CreateVendaFinalRequest {
  valor: number;
  status: StatusVendaFinal;
  type: TipoPagamento;
  vencimento: Date;
  dataPagamento?: Date;
  vendaId: number;
  id_payment?: string;
  copiacola?: string;
  qrcode?: string;
  codigobarra?: string;
  gatewayId?: number;
  conexaoId?: number;
}

export interface UpdateVendaFinalRequest extends Partial<CreateVendaFinalRequest> { }

export const vendaFinalService = {
  async create(data: CreateVendaFinalRequest): Promise<ApiResponse<VendaFinal>> {
    const response = await api.post<ApiResponse<VendaFinal>>('/vendas-finais', data);
    return response.data;
  },

  async list(params: { 
    page?: number; 
    limit?: number; 
    search?: string; 
    vendaId?: number;
    type?: TipoPagamento;
    status?: StatusVendaFinal;
    vencimento?: Date | string;
    gatewayId?: number;
    orderBy?: 'createdAt_desc' | 'createdAt_asc' | 'vencimento_desc' | 'vencimento_asc' | 'valor_desc' | 'valor_asc';
  } = {}): Promise<PaginationResponse<VendaFinal[]>> {
    const { page = 1, limit = 10, search, vendaId, type, status, vencimento, gatewayId, orderBy } = params;
    const response = await api.get<PaginationResponse<VendaFinal[]>>('/vendas-finais', {
      params: { 
        page, 
        limit, 
        search, 
        vendaId, 
        type, 
        status, 
        vencimento: vencimento,
        gatewayId,
        orderBy
      }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<VendaFinal>> {
    const response = await api.get<ApiResponse<VendaFinal>>(`/vendas-finais/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateVendaFinalRequest): Promise<ApiResponse<VendaFinal>> {
    const response = await api.put<ApiResponse<VendaFinal>>(`/vendas-finais/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/vendas-finais/${id}`);
    return response.data;
  },

  async getTotal(): Promise<ApiResponse<number>> {
    const response = await api.get<ApiResponse<number>>('/vendas-finais/count/total');
    return response.data;
  },

  async getByVendaId(vendaId: number): Promise<ApiResponse<VendaFinal[]>> {
    const response = await api.get<ApiResponse<VendaFinal[]>>(`/vendas-finais/venda/${vendaId}`);
    return response.data;
  },

  // Enviar fatura para o cliente
  async send(id: number): Promise<ApiResponse<any>> {
    const response = await api.post<ApiResponse<any>>(`/vendas-finais/${id}/send`);
    return response.data;
  },

  // Gerar fatura no gateway
  async generate(id: number): Promise<ApiResponse<any>> {
    const response = await api.post<ApiResponse<any>>(`/vendas-finais/${id}/generate`);
    return response.data;
  }
};
