import { useEffect, useState, useCallback } from 'react';

export interface AppConfig {
  appName: string;
  usarLandingPage: boolean;
  apiEnv: string;
  backendUrl: string;
}

// Cache global
let cachedConfig: AppConfig | null = null;
let configPromise: Promise<AppConfig> | null = null;
let lastFetchTime: number = 0;

// Fallbacks melhorados
const defaultConfig: AppConfig = {
  appName: 'CobPanel',
  usarLandingPage: false,
  apiEnv: 'production',
  backendUrl: 'http://localhost:5555'
};

async function fetchConfig(forceRefresh = false): Promise<AppConfig> {
  const currentTime = Date.now();
  
  // Se tem cache e não está forçando refresh e não passou 5 minutos
  if (cachedConfig && !forceRefresh && (currentTime - lastFetchTime) < 300000) {
    return cachedConfig;
  }

  if (configPromise && !forceRefresh) {
    return configPromise;
  }

  configPromise = fetch('/api/config', {
    cache: forceRefresh ? 'no-cache' : 'default',
    headers: {
      'Content-Type': 'application/json'
    }
  })
    .then(async res => {
      if (!res.ok) {
        throw new Error(`HTTP ${res.status}: ${res.statusText}`);
      }
      return res.json();
    })
    .then(config => {
      // Validar estrutura da config
      const validatedConfig: AppConfig = {
        appName: config.appName || defaultConfig.appName,
        usarLandingPage: Boolean(config.usarLandingPage),
        apiEnv: config.apiEnv || defaultConfig.apiEnv,
        backendUrl: config.backendUrl || defaultConfig.backendUrl
      };
      
      cachedConfig = validatedConfig;
      lastFetchTime = currentTime;
      return validatedConfig;
    })
    .catch(error => {
      // console.warn('[useAppConfig] Erro ao buscar configurações, usando defaults:', error);
      
      // Se já temos cache, usar ele mesmo com erro
      if (cachedConfig && !forceRefresh) {
        return cachedConfig;
      }
      
      // Usar defaults como último recurso
      cachedConfig = defaultConfig;
      lastFetchTime = currentTime;
      return defaultConfig;
    })
    .finally(() => {
      configPromise = null;
    });

  return configPromise;
}

export function useAppConfig() {
  const [config, setConfig] = useState<AppConfig | null>(cachedConfig);
  const [loading, setLoading] = useState(!cachedConfig);
  const [error, setError] = useState<string | null>(null);

  const refreshConfig = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const newConfig = await fetchConfig(true);
      setConfig(newConfig);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Erro desconhecido');
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (!cachedConfig || !cachedConfig.backendUrl || cachedConfig.backendUrl.includes('localhost')) {
      fetchConfig()
        .then(setConfig)
        .catch(err => {
          setError(err instanceof Error ? err.message : 'Erro ao carregar configuração');
        })
        .finally(() => setLoading(false));
    }
  }, []);

  return { 
    config, 
    loading, 
    error, 
    refreshConfig,
    isConfigured: !!cachedConfig
  };
}

// Função para obter config de forma síncrona (se já estiver em cache)
export function getAppConfig(): AppConfig | null {
  return cachedConfig;
}

// Função para obter config de forma assíncrona
export async function loadAppConfig(forceRefresh = false): Promise<AppConfig> {
  return fetchConfig(forceRefresh);
}

// Função para invalidar cache
export function invalidateConfigCache(): void {
  cachedConfig = null;
  lastFetchTime = 0;
  configPromise = null;
}

// Hooks especializados para valores específicos
export function useAppName(): string {
  const { config } = useAppConfig();
  return config?.appName || defaultConfig.appName;
}

export function useBackendUrl(): string {
  const { config } = useAppConfig();
  // console.log('config?.backendUrl-useBackendUrl:', config?.backendUrl);
  return config?.backendUrl || defaultConfig.backendUrl;
}

export function useSocketUrl(): string {
  return useBackendUrl(); // Socket usa mesma URL do backend
}

export function useApiBaseUrl(): string {
  const backendUrl = useBackendUrl();
  return `${backendUrl}/api/v1`; // API é sempre backend + /api/v1
}

export function useIsLandingPageEnabled(): boolean {
  const { config } = useAppConfig();
  return config?.usarLandingPage || defaultConfig.usarLandingPage;
}

export function useIsDevelopment(): boolean {
  const { config } = useAppConfig();
  return (config?.apiEnv || defaultConfig.apiEnv) === 'development';
}